import useInput from '@hooks/useInput';
import { Button, Error, Form, Header, Input, Label, LinkContainer } from '@pages/member/SignUp/styles';
import React, { useCallback, useEffect, useState } from 'react';
import useCustomMember from '@hooks/useCustomMember';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';

const LogIn = () => {
  const { isLogin, doLogin, updateUrlAfterLogin } = useCustomMember();

  const [logInError, setLogInError] = useState(false);
  const [email, onChangeEmail] = useInput('');
  const [password, onChangePassword] = useInput('');

  const onSubmit = useCallback(
    async (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      setLogInError(false);

      try {
        const result = await doLogin({ email, password });

        if (result.success) {
          toast.dismiss();
          toast.success('Login successful');
          await updateUrlAfterLogin();
        } else {
          setLogInError(true);
          toast.error(result.message || 'Login failed');
        }
      } catch (error) {
        setLogInError(true);
        console.error('Login error:', error);
        toast.error('Login failed');
      }
    },
    [doLogin, email, password, updateUrlAfterLogin],
  );

  useEffect(() => {
    if (isLogin) {
      updateUrlAfterLogin().then();
    }
  }, [isLogin, updateUrlAfterLogin]);

  return (
    <div id="container">
      <Header>MQ-Navigator</Header>
      <Form onSubmit={onSubmit}>
        <Label id="email-label">
          <span>Email Address</span>
          <div>
            <Input type="email" id="email" name="email" value={email} onChange={onChangeEmail} />
          </div>
        </Label>
        <Label id="password-label">
          <span>Password</span>
          <div>
            <Input type="password" id="password" name="password" value={password} onChange={onChangePassword} />
          </div>
          {logInError && <Error>The combination of email and password does not match.</Error>}
        </Label>
        <Button type="submit">Log In</Button>
      </Form>
      <LinkContainer>
        Not a member yet?&nbsp;
        <Link to="/member/signup">Sign Up Here</Link>
      </LinkContainer>
    </div>
  );
};

export default LogIn;
